/* Prevent any margin, scrollbar, or background gaps */
html, body, #root, .App {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  background-color: #111;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Book container fills available space and stays centered */
.single-page-book {
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.5);
  background-color: #222;
}

/* Each page in the flipbook */
.page {
  width: 100%;
  height: 100%;
  background: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Placeholder blank pages */
.blank-page {
  background-color: transparent;
}

/* Full image with shadow and centered */
.flip-img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  border-radius: 12px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.6);
}
